.form_item {
  :global {
    :local(.form_item) {
      &.ant-form-item-with-help {
        margin-bottom: 14px;
      }
    }
    .ant-form-item-label {
      label {
        max-width: 100%;
        height: auto;
        &.ant-form-item-required {
          &::before {
            position: absolute;
            top: 4px;
          }
          span {
            margin-left: 6px;
          }
        }
        span {
          white-space: normal;
        }
      }
    }
  }
}
